const searchField = {
  functional: true,
  props: [],
  render (h, { parent: vm, props }) {
    return (
        <form staticClass="uk-form uk-text-right">
          <div class="uk-form-icon">
              <i class="uk-icon-search"></i>
              <input type="text" on-keyup={e => {
                vm.search(e.target.value)
              }}/>
          </div>
        </form>
    )
  }
}
export default searchField




